const state = {
    reglas: {
        requerido: (v) => !!v || "Este campo es requerido.",  // CAMPO REQUERIDO
        correo: (v) => /.+@.+\..+/.test(v) || "El correo debe ser vlido", //CORREO VLIDO
        DNI: (v) => (v && v.length == 8) || 'El DNI debe ser de 8 dgitos.', // DNI VLIDO
        edad: (v) => (v && v >= 18  ) || 'Debe ser mayor de edad.', //EDAD VLIDA
        minimochar : (v) => (v && v.length >= 8)  || 'Mnimo 8 carcteres.', //MINIMO CARCTERES
        pass : (v) => /^(?=.*\d)(?=.*[a-z]).*[A-Z]/.test(v) || 'Debe contener por lo menos una mayscula, una minscula y un nmero.',  //CONTRASEA ESPECIAL
        numCelular: (v) => (v && v.length == 9) || 'Debe ser un nmero vlido.', // DNI VLIDO
        celular: (v) => /9+(?=.*\d)/.test(v) || 'Debe ser un nmero vlido.',  //CONTRASEA ESPECIAL
    },

    alerta: {
        mensajeAlerta: null, //MENSAJE QUE CONTIENE LA ALERTA
        tipoAlerta: null, //EL TIPO DE ALERTA
    }
    
};

const getters = {
    //CONSIGUE TODAS LAS REGLAS
    getReglas(state){
        return state.reglas
    },
    //CONSIGUE EL MENSAJE DE LA ALERTA
    getAlert(state){
        return state.alerta
    }

};

const mutations = {
    //PONE EL MENSAJE Y EL TIPO DE ALERTA
    setAlerta(state,payload){
        state.alerta = payload
    }
};

const actions = {
    //LLAMA AL SETTER DE LA ALERTA
    mensajeTipoAlert({commit},alerta){
        commit('setAlerta',alerta)
    }
};

export default {
    state,
    getters,
    mutations,
    actions,
}